import React from 'react';
import Home from "./jsx/Home";
import ParticlesBg from "particles-bg";
import './css/App.css';

function App() {
  let config = {
    num: [4, 7],
    rps: 0.1,
    radius: [3, 20],
    life: [1.5, 3],
    v: [2, 3],
    tha: [-50, 50],
    alpha: [0.6, 0],
    scale: [.1, 0.9],
    position: "all",
    color: ["random", "#ff0000"],
    cross: "dead",
    random: 10
  };

  if (Math.random() > 0.85) {
    config = Object.assign(config, {
      onParticleUpdate: (ctx, particle) => {
        ctx.beginPath();
        ctx.rect(
          particle.p.x,
          particle.p.y,
          particle.radius * 2,
          particle.radius * 2
        );
        ctx.fillStyle = particle.color;
        ctx.fill();
        ctx.closePath();
      }
    });
  };
  
  return (
    <div className="App">
      <Home />
      <ParticlesBg type="custom" config={config} bg={true}/>
    </div>
  );
}

export default App;
