import React from 'react';
import { NavLink  } from 'react-router-dom';
import HomeIcon from '@material-ui/icons/Home';
import InfoIcon from '@material-ui/icons/Info';
import PersonIcon from '@material-ui/icons/Person';
import AssessmentIcon from '@material-ui/icons/Assessment';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
    nav:{
        width: "100%",
        position: "fixed",
        top: 0,
        zIndex: 9,
        height:"60px",
        display: "inline-flex",
        justifyContent: "flex-end",
        alignItems: "center",
        backgroundColor:"#CAE7DF",
        borderBottom:"2px solid #FF4929"
    },
    link:{
        padding: "0 20px",
        textDecoration: "none",
        fontSize: "16px",
        fontStyle: "normal",
        textTransform: "uppercase",
        color: "#FF4929",
        display: "inline-flex",
        justifyContent: "center",
        alignItems: "center",
        "&:hover": {
            color:"#FF9B70"
        },     

    }
}));

export default function Header() {
    const classes = useStyles();  
    return (
        <nav className={classes.nav}>           
            <NavLink  to="/" className={classes.link}><HomeIcon/>Home</NavLink >
            <NavLink  to="/about" className={classes.link} activeStyle={{ color:'#fff'}}> <InfoIcon />ABOUT</NavLink >
            <NavLink  to="/portfolio" className={classes.link} activeStyle={{ color:'#fff'}}> <AssessmentIcon />PORTFOLIO</NavLink >
            <NavLink  to="/contact" className={classes.link} activeStyle={{ color:'#fff'}}> <PersonIcon />CONTACT </NavLink >
        </nav>
        
    );
}