import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
    container:{ 
        paddingTop:'120px',
        // backgroundColor:'#EDF8F5',
    },
    group:{
        margin:'0 auto',
        maxWidth:'1200px'
    },
    header:{
        fontSize: '24pt',
        color: '#444649',
        fontFamily: "raleway-bold",
        textAlign:"center",
        lineHeight: "18pt"
    },
    headerBar:{
        background: "#444649",
        height: "4px",
        width: "70px",
        display: "block",
        margin: "0 auto 24px"
    },
    profile:{
        width:"200px",
        display: "block",
        margin: "0 auto",
        clipPath: "polygon(75% 0, 100% 50%, 75% 100%, 25% 100%, 0 50%, 25% 0)",        
    },
    image: {
        position: 'relative',
        height: 300,
        marginBottom: '5px',
        [theme.breakpoints.down('sm')]: {
          height: 240,
          marginBottom: '3px',
        },
        [theme.breakpoints.down('xs')]: {
          width: '100% !important', // Overrides inline-style
          height: 200,
        },
        '&:hover, &$focusVisible': {
          zIndex: 1,
          '& $imageBackdrop': {
            opacity: 0.7,
          },
          '& $imageMarked': {
            opacity: 1,
          },
          '& $imageTitle': {
            opacity:'1',
            border: '4px solid currentColor',
          },
        },
      },
      focusVisible: {},
      imageButton: {
        position: 'absolute',
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        color: '#FF4929',
      },
      imageSrc: {
        position: 'absolute',
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        backgroundSize: '100%',
      },
      imageBackdrop: {
        position: 'absolute',
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        backgroundColor: '#fff',
        opacity: 0,
        transition: theme.transitions.create('opacity'),
      },
      imageTitle: {
        position: 'relative',
        opacity:'0',
        padding: `${theme.spacing(2)}px ${theme.spacing(4)}px ${theme.spacing(1) + 6}px`,
      },
      

}));
export default useStyles;