import React from 'react';
import Header from './jsx/Header';
import Style from './jsx/style';



function About() {
    const classes = Style(); 
    return (        
        <div className="About">
            <Header />
            <div className={classes.container}>
                <h1 className={classes.header}>ABOUT</h1>
                <span className={classes.headerBar} />
                <img src="/images/profile.jpg" alt="image" className={classes.profile} />
            </div>
        </div>
    );
}
export default About;