import React from 'react';
import Header from './jsx/Header';
import Style from './jsx/style';
import Typography from '@material-ui/core/Typography';
import ButtonBase from '@material-ui/core/ButtonBase';

const images = [
    {
      url: 'images/project_01.png',
      title: 'Marugoto',
      width: '33%',
      link:'https://www.marugoto.org/en/'
    },
    {
      url: 'images/project_02.png',
      title: 'JapanByJapan',
      width: '33%',
      link:'https://japanbyjapan.com/'
    },
    {
        url: 'images/project_02.png',
        title: 'Burgers1',
        width: '33%',
        link:'https://japanbyjapan.com/'
    },
    {
        url: 'images/project_01.png',
        title: 'Marugoto1',
        width: '33%',
        link:'https://www.marugoto.org/en/'
    },
    {
        url: 'images/project_01.png',
        title: 'Marugoto2',
        width: '33%',
        link:'https://www.marugoto.org/en/'
    },
    {
        url: 'images/project_02.png',
        title: 'Burgers2',
        width: '33%',
        link:'https://japanbyjapan.com/'
    },
];
  

function Portfolio() {
    const classes = Style(); 
    return (        
        <div className="Portfolio">
            <Header />
            <div className={classes.container}>
                <h1 className={classes.header}>PORTFOLIO</h1>
                <span className={classes.headerBar} />
                <div className={classes.group}>
                {images.map(image => (
                    <ButtonBase
                    focusRipple
                    key={image.title}
                    className={classes.image}
                    focusVisibleClassName={classes.focusVisible}
                    disableTouchRipple={true}
                    style={{
                        width: image.width,
                    }}
                    href = {image.link}
                    >
                    <span
                        className={classes.imageSrc}
                        style={{
                        backgroundImage: `url(${image.url})`,
                        }}
                    />
                    <span className={classes.imageBackdrop} />
                    <span className={classes.imageButton}>
                        <Typography
                        component="span"
                        variant="subtitle1"
                        color="inherit"
                        className={classes.imageTitle}
                        >
                        {image.title}
                        </Typography>
                    </span>
                    </ButtonBase>
                ))}
                </div>                
            </div>
        </div>
    );
}
export default Portfolio;