import React from 'react';
import { NavLink } from 'react-router-dom';
import Container from '@material-ui/core/Container';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import { makeStyles } from '@material-ui/core/styles';
import Typical from 'react-typical';

const useStyles = makeStyles(theme => ({
  '@global': {
    body: {
      backgroundColor: '#CAE7DF',
      color:'rgb(97, 97, 97)',
      fontFamily: 'Gelasio',
      margin:0
    },
  },
  colorRed:{
        backgroundColor:'red'
  },
  paper: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent:'center',
    color:'#FF4929 !important',
    fontSize:'26px',  
    height:'640px',
    fontWeight:'bold',
  },
   getStart:{
       fontSize:'16px',
       marginTop: theme.spacing(3),
       borderColor: '#FF9B70',
       background:'#FF9B70',
       color: '#FFF',
       width:'180px',
       padding:'6px 0',
       textDecoration:'none',
       minWidth: '64px',
       boxSizing: 'border-box',
       fontFamily: "Roboto, Helvetica ,Arial",
       fontWeight: 500,
       lineHeight: 1.75,
       borderRadius: '4px',
       letterSpacing: '0.02857em',
       textTransform: 'uppercase',
       display:'flex',
       alignItems:'center',
       justifyContent:'center',
       "&:hover": {
          background: "#fff",
          color:"#FF9B70"
        },
   }
}));
const steps = [
  'HELLO ðŸ‘‹', 1000,
  'WELCOME FROM MY PORFOLIO', 2000,
  'I AM WAI WAI NAING', 2000,
  'I AM FULL STACK DEVELOPER', 2000,
  'I HELP DESIGNERS, SMALL AGENCIES AND BUSINESS BRING THEIR IDEAS TO LIFE.', 2000
];
export default function Header() {
  const classes = useStyles();  

  return (
        <Container component="main" id="home">
          <div className={classes.paper}>
            <Typical wrapper="span" steps={steps} loop={Infinity} className={'caca'} />   
            <NavLink to="/about" className={classes.getStart}>
                Getting Start    
                <ArrowForwardIcon/>
            </NavLink>            
          </div>
        </Container>  
  );
}